<template>
	<view class="detail">
		<view class="title">{{detail.title}}</view>
		<view class="info">
			<view class="author">{{detail.author}}</view>
			<!-- 后端发送过来的detail.posttime的值是一个时间戳，需要转成正常时间 -->
			<view class="time">{{detail.posttime}}</view>
		</view>
		<view class="content">
			<!-- 后端发送过来的detail.content的值是一个带有<p>标签的字符串，所以不能直接用{{detail.content}}来展示该数据，需要借用uniapp里面提供的组件标签rich-text来将字符串转成富文本标签 -->
			<!-- 之所以后端传过来的数据是富文本字符串，是因为借助rich-text标签后，就能直接将富文本字符串数据变成带有图片与文字并且格式对排好了的内容，我觉得这应该属于前端的工作 -->
			<rich-text :nodes="detail.content"></rich-text>
			
		</view>
		<view class="description">
			免责声明:本站出现的所有内容仅供学术交流使用,禁止产生利益,违者后果自负,与我无关.
		</view>
		
	</view>
</template>

<script >
	import dayjs from "dayjs"
	export default {
		data() {
			return {
				options:null,
				detail:{}
			};
		},
		onLoad(e) {
			// 形参e就是从父组件newsbox传过来的数据，即每个文章的classname、id两个键值对，利用这俩键值对向后端发请求，就能准确获取这篇新闻内容的详细信息
			this.options=e;
				console.log(e);
			this.getDetail()
		},
		onShow() {
			this.getDetail()
		},
		methods:{
			getDetail(){
				uni.request({
					url:"https://ku.qingnian8.com/dataApi/news/detail.php",
					// 因为this.options的值就是一个含有键值对classname、id的对象，data的值要求是一个对象，所以可以这么写,意思为带着数据this.options向后端发送请求
					data:this.options, 
					success:res=>{
							console.log(res.data);
						res.data.posttime=dayjs(res.data.posttime).format('YYYY-MM-DD HH:mm:ss')
						// 这活本应给前端做，就用不着这么麻烦把接收到的数据res.data.content中存着的带有标签的字符串里面的所有<img字符串替换成<img style="max-width:100%"了，字符串res.data.content调用replace方法，会检索res.data.content中所有含有第一个形参的字符串，然后全部替换成第二个形参。
						// gi是正则表达式
						console.log(res.data.content);
						res.data.content=res.data.content.replace(/<img/gi,'<img style="max-width:100%"')
						this.detail=res.data,
						// 每当用户浏览一个新闻后，执行savehistory方法，将该新闻保存到本地存储localstorage里面
						this.saveHistory(),
						// 改变页面最顶部的标题
						uni.setNavigationBarTitle({
							title:this.detail.title
						})
					}
				})
			},
			// 倒数第二个需求：用户点击新闻后，浏览过的新闻放到个人中心的历史记录里面。
			// uni.setStorageSync(a,b)方法执行后会（同步方法）在本地存储中存放属性名为该方法的第一个形参、属性值为该方法第二个形参的键值对。uni.getStorageSync(a,b)方法执行后会从本地缓存（右键网页-检查-application-local storage）中获取属性名为该方法第一个形参的键值对的属性值
			saveHistory(){
				// 获取本地存储中属性名叫historyarr的属性值，如果没有就创建个空数组，再赋值给变量historyarr
				let historyArr=uni.getStorageSync("historyArr")||[]
				// 往数组historyArr中放当前新闻详情页展示的新闻的calsstime等数据。相当于把新闻列表组件newbox组件中要展示的所有数据（图片、标题、时间）和该组件绑定的响应函数goDetail中发送请求时需要携带的参数calssname、id都存到item变量中去
			let item={
				id:this.detail.id,
				classid:this.detail.classid,
				picurl:this.detail.picurl,
				title:this.detail.title,
				looktime:dayjs(Date.now())
			}
			// 最后一个需求：去掉浏览历史中重复的新闻记录。解决思路：只有点进新闻详情页才会在本地存储中保存当前新闻的数据信息，那么在保存前，让当前新闻详情页展示的新闻的id数据跟此时本地存储中historyArr的属性值中的所有元素中的id做对比，如果有相同，则删除该id的元素，再往后执行在数组historyArr的最开始位置存储当前新闻详情页展示的新闻信息即可。这样一来不仅不会出现重复的浏览记录，而且已经浏览过的新闻如果用户再次浏览，那么旧的浏览记录会被删除，并且该条浏览记录会重新出现在历史记录的第一条。另一种思路是修改本地存储中重复新闻记录元素中的时间，比较用户重新浏览了浏览过的新闻，那么这两条记录里面只有浏览时间looktime不一样。在本地存储中通过looktime来保留新的那条浏览记录即historyArr中的元素
			// findIndex（箭头函数）方法中的箭头函数中的return返回的表达式的值为true时，该方法findindex的返回值就是此时遍历到的元素的索引值（即返回该元素是数组中索引值为？的元素），反之，findindex函数就会返回-1，代表数组中没有满足箭头函数中表达式的元素。
			// 在向historyArr变量中存新的item前，先让histArr经过findindex方法遍历一遍，如果出现数组histArr中的某个元素中的id的值跟上面63行的item（即此时详情页展示的新闻的信息对象）中的id的值是否一样，如果一样（findindex方法会返回一样的那个元素的索引值），删掉historyArr中的该元素
			let index=historyArr.findIndex(i=>{
				return i.id==this.detail.id
			})
			if(index>=0){
				// index>=0代表有重复新闻，index的值就是historyarr数组中重复新闻的元素的索引值，数组。splice（a,b）方法，从数组第a个索引值位置开始删，往后删b个元素
				historyArr.splice(index,1)
			}
			// 将当前item对象放到此时的数组historyArr里面。这样一来每当用户浏览一篇新闻后，本地缓存中属性historyarr的属性值即数组里面就会多一个元素item，每个元素都分别保存这=着用户浏览过的新闻数据.unshift()在数组最开始位置存放元素
			historyArr.unshift(item)
			
			// 如果嫌本地存储中的浏览历史太多，不想一次性都展示出来，可以用slice截取前十条，再存进去
			historyArr=historyArr.slice(0,10);
			// 借助setStroageSync方法将保存了更多item元素的变量historyArr的值存到本地存储中属性名为historyArr的属性值里面，会覆盖原先本地存储中属性名为historyArr的值
			uni.setStorageSync("historyArr",historyArr)
		   }
		}
	
	}
</script>

<style lang="scss">
  .detail{
		padding: 30rpx;
		.title{
			font-size: 46rpx;
			color:#333;
		}
		// <!-- 文章编辑,时间的信息样式 -->
		.info{
			background:#f6f6f6;
			padding:20rpx;
			margin: 20rpx 0;
			font-size:25rpx;
			color:#666;
			display: flex;
			justify-content: space-between;
		}
		.content{
			padding-bottom:50rpx;
			/deep/ img{
				max-width: 100%;
			}
		}
		.description{
			background: #fef0f0;
			font-size: 26rpx;
			padding: 20rpx;
			color:#f89898;
			line-height: 1.8em;
		}
	}
</style>
